/* font family */
@font-face {
    font-family: "PretendardRegular";
    src: local("PretendardRegular"),
        url("./fonts/Pretendard-Regular.otf") format("truetype");
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h2,
h4,
h6,
p,
ul {
    margin-bottom: 0;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

a {
    display: inline-block;
    text-decoration: none;
}

.tickIcon {
    font-size: 100px;
    color:#05ffff;
}

/* bootstrap css fixed */
.container {
    max-width: 2500px;
    padding: 3vh 5vw;
}

/* pages stylesheet */
.img-amin-box,
.roadmap-box {
    padding-top: 130px;
}

.readmap-content p {
    font-size: 1.6em;
    font-weight: 400;
    padding: 20px 0;
    color: #000;
}

.readmap-content h2 {
    font-size: 6.5em;
    color: #000;
    line-height: 1;
    font-weight: 600;
}

/* utility */
.utility-box {
    margin-top: 60px;
    height: 100vh;
    overflow: hidden;
}

.img-amin-box {
    position: relative;
}


.img-box-1 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-10deg);
}

.img-box-2 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(10deg);
}

.img-box-3 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-20deg);
}

.img-box-4 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(20deg);
}

.img-box-5 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-6deg);
}



.utility-info h2 {
    font-size: 20vh;
}

.utility-info h2:last-of-type {
    margin-left: 50%;
}

.ulitity-info-bottom h2 {
    font-size: 3.5em;
    color: #000;
}

.ulitity-info-bottom p {
    font-size: 1.6em;
    font-weight: 500;
    color: #000;
    padding: 20px 0;
}

.utility-info-content h2 {
    font-size: 4em;
    color: #000;
}


/* raffle page */

.raffle-box {
    background-color: #000;
    margin-top: 100px;
}

.raffle-left h2 {
    font-size: 60px;
    font-weight: 600;
    color: #fff;
    font-family: 'Bebas Neue', cursive;
    display: inline-block;
    padding-right: 20px;
    letter-spacing: 20px;

}

.raffle-right {
    border-left: 1px solid #fff;
    padding-left: 20px;
}

.raffle-right h6 {
    font-size: 40px;
    font-weight: 400;
    color: #fff;
    font-family: 'Bebas Neue', cursive;
    letter-spacing: 10px;
}

.raffle-social-content .btn,
.raffle-right .btn {
    font-size: 17px;
    font-weight: 600;
    border: 1px solid yellow;
    color: yellow;
    width: 172px;
    padding: 5px;
    text-transform: uppercase;
}

.raffle-social-content .btn:hover,
.raffle-right .btn:hover {
    background-color: yellow;
    color: #000;
    font-weight: 700;
}

.raffle-right p {
    font-size: 26px;
    font-weight: 400;
    color: #fff;
    padding-top: 20px;
}

.raffle-user-verify ul li a,
.raffle-social ul li a {
    font-size: 27px;
    color: #000;
    background-color: yellow;
    height: 37px;
    width: 37px;
    border-radius: 5px;
    line-height: 30px;
    text-align: center;
    transition: all .3s;
}

/* .raffle-user-verify ul li a:hover, */
.raffle-social ul li a:hover {
    background-color: #222;
    color: #fff;
}

.raffle-social-content a {
    font-size: 23px;
    color: #dcff36;
    text-decoration: none;
}

.raffle-user-verify ul li {
    color: yellow;
    font-size: 19px;
}

.raffle-user-verify-field ul li {
    font-size: 20px;
    color: #fff;
}

.raffle-user-verify-field ul li input {
    background: none;
    border: none;
    border-bottom: 1px solid #fff;
    padding: 4px 10px;
    color: #fff;
    width: 100%;
}

.raffle-user-verify-field ul li input:focus {
    outline: none;
    background-color: #fff;
    color: #222;
}

.raffle-re-tweet p {
    padding-top: 0;
}

.raffle-re-tweet ul li:last-child .btn{
    /* font-size: 16px; */
}


@media screen and (max-width: 973px) {
    .readmap-content h2 {
        font-size: 50px;
    }

    .img-amin-box img {
        max-height: 40vh;
        max-width: 40vh;
    }

    .utility-info h2:last-of-type {
        margin-left: 0;
    }

    .utility-info-content h2 {
        font-size: 3em;
    }

    .ulitity-info-bottom h2 {
        font-size: 6vh;
    }

    .utility-info h2 {
        font-size: 12vh;
    }
    .raffle-right h6 {
        font-size: 28px;
    }
}